export class Queue {
  constructor(maxSize) {
    this.queue = [];
    this.maxSize = maxSize;
  }

  //    
  enqueue(item) {
    if (this.queue.length >= this.maxSize) {
      this.queue.shift(); //   maxSize     
    }
    this.queue.push(item); //    
  }

  //    
  dequeue() {
    if (this.isEmpty()) {
      console.log("Queue is empty");
      return null;
    }
    return this.queue.shift(); //      
  }

  //       ( )
  peek() {
    if (this.isEmpty()) {
      console.log("Queue is empty");
      return null;
    }
    return this.queue[0];
  }

  //    
  isEmpty() {
    return this.queue.length === 0;
  }

  //     
  size() {
    return this.queue.length;
  }

  //     
  printQueue() {
    console.log(this.queue);
  }

  getQueue() {
    return this.queue;
  }
}
